.App {
	text-align: center;
	background: linear-gradient(140deg, #ffddea 0%, #f5cedd 100%);
	min-height: 100svh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.screen {
	display: grid;
	grid-template-areas: "header" "content" "footer";
	grid-template-columns: 1fr;
	grid-auto-rows: minmax(2em, auto);
	gap: 1em;
	color: #b47f87;

	.screen-header {
		grid-area: header;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 1em 2em;
	}

	.screen-content {
		grid-area: content;
	}

	.screen-footer {
		grid-area: footer;
		padding-bottom: 2em;
	}
}

